body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 4px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

/*
 * --- spicing up components ---
 */
/* square:
 - bigger size and font
 - nice background color & transition
 - pointer cursor
*/
.square {
  font-size: 4rem;
  height: 140px;
  width: 140px;
  color: #cfc8b8;
  border: 4px solid #cfc8b8;
}

.square:hover {
  transition: background-color 0.5s ease;
  background-color: #e6e2da;
  cursor: pointer;
}

.square.x {
  background-color: #524a4c;
}

.square.o {
  background-color: #6b948c;
}

.board {
  position: relative;
  border: 15px solid #cfc8b8;
  box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.75);
  z-index: 10;
}

/* Game:
 - center stage it
*/
.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  height: 100vh;
  border: 3px solid;
}

.game-board {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex: 0.7 1;
          flex: 0.7 1;
  background-image: url("https://images.unsplash.com/photo-1542836717-d704523b0b4e?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=667&q=80");
  background-size: cover;
  background-position: 0 -50px;
}

.game-board:after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 0;
}

.game-info {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 0.3 1;
          flex: 0.3 1;
}

.game-info__status,
.game-info__moves {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 1rem;
}

.game-info__status {
  font-size: 2rem;
}

.game-info__status {
  color: rgb(20, 2, 2);
  background-color: #ffff;
}

.game-info__moves {
  background-color: #080002;
}

.game-info__moves .list-moves li {
  margin-bottom: 0.5rem;
}

h3 {
  text-align: center;
}

.bounce {
  background: #bd081c;
  text-align: center;
  padding: 1rem;
  padding: 1rem;
  font-size: 2rem;
  -webkit-transform: rotate(-25deg) translateX(-20px) translateY(100px);
  transform: rotate(-25deg) translateX(-20px) translateY(100px);
  transition: 0.4s all;
}

